@import "style/mixins.module.scss";

.root {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 40px;

    @include respond-to("laptop") {
        grid-template-columns: repeat(12, 1fr);
    }
}

.column {
    display: grid;
    position: relative;

    @include respond-to("mobile") {
        justify-content: center;

        &_with-divider::after {
            position: absolute;
            bottom: 30px;
            right: -20px;
            content: "";
            width: 1px;
            height: 100%;
            max-height: calc(100% - 24px);
            background-color: rgba(66, 78, 88, 0.1);
        }

        &_with-divider-teeth::after {
            max-height: 250px;
        }

        &__payment-once {
            margin-bottom: 43px;
            grid-row: 3;

            &::after {
                bottom: 0;
                max-height: none;
            }
        }

        &__payment-monthly {
            margin-bottom: 40px;
            grid-row: 5;

            &::after {
                bottom: 10px;
            }
        }

        &.main-content {
            &::after {
                content: none;
            }
        }
    }

    @include respond-to("laptop") {
        grid-column: span 4;

        &_regular {
            grid-column: 5 / span 4;
        }

        &_flex {
          display: flex;
          flex-direction: column;
        }
    
    }

    @include respond-to("laptop-L") {
        &_regular {
            grid-column: 4 / span 4;
        }

        &_extended {
            grid-column: 9 / span 4;
        }
    }
}

.merged-column {
    display: grid;
    grid-row: inherit;
    grid-column: 1 / span 2;

    @include respond-to("mobile") {
        justify-items: center;
    }

    @include respond-to("laptop") {
        grid-column: 1 / span 4;
        justify-self: flex-start;
    }

    @include respond-to("laptop-L") {
        grid-column: 1 / span 3;
    }
}

.treatment {
    @include respond-to("mobile") {
        grid-row: 10;
        margin-top: 24px;

        &_with-divider {
            grid-row: 12;
        }
    }

    @include respond-to("laptop") {
        display: grid;
        align-content: flex-start;
        grid-row: span 3;
        padding-top: 126px;
    }

    @include respond-to("laptop-L") {
        justify-self: unset;
    }

    &__title {
        margin-bottom: 32px;

        @include respond-to("laptop") {
            margin-bottom: 24px;
        }
    }
}

.description {
    margin-bottom: 30px;
    font-size: 17px;
    line-height: 24px;

    @include respond-to("mobile") {
        text-align: center;
    }

    @include respond-to("laptop") {
        font-size: 22px;
        line-height: 32px;
    }
}

.teeth {
    display: flex;
    text-align: center;

    @include respond-to("mobile") {
        flex-direction: column;
    }

    @include respond-to("laptop") {
        padding-bottom: 36px;
        border-bottom: 1px solid rgba(66, 78, 88, 0.1);
        margin-bottom: 29px;
    }

    &__item {
        @include respond-to("laptop") {
            width: 33%;
        }

        @include respond-to("laptop-L") {
            & + & {
                margin-left: 24px;
            }
        }
    }

    &__icon {
        height: 40px;
        margin-bottom: 8px;
    }

    &__title {
        margin-bottom: 24px;
        font-size: 13px;
        line-height: 20px;

        @include respond-to("laptop") {
            max-width: 90px;
            margin: 0 auto;
            font-size: 17px;
            line-height: 24px;
        }
    }
}

.duration {
    margin-bottom: 30px;

    &__title {
        margin: 0;
    }

    &__description {
        margin: 0;
        font-size: 17px;
        line-height: 24px;

        @include respond-to("laptop") {
            display: none;
        }
    }
}

.duration-value {
    margin-bottom: 24px;
    font-size: 17px;
    line-height: 24px;
    align-items: flex-start;

    @include respond-to("mobile") {
        margin-bottom: 20px;
        grid-row: 2;
        text-align: center;

        strong {
            font-family: Inter, sans-serif;
        }

        &::after {
            content: none;
        }
    }

    @include respond-to("laptop") {
        margin-bottom: 30px;
        padding-bottom: 36px;
        border-bottom: 1px solid rgba(66, 78, 88, 0.1);
        font-size: 22px;
        line-height: 32px;
    }
}

.payment {
    margin-bottom: 8px;

    @include respond-to("mobile") {
        grid-row: 4;

        &_once {
            margin-top: 24px;
        }

        &_monthly {
            text-align: center;
            margin-bottom: 16px;
            font-size: 17px;
            line-height: 24px;
        }
    }

    @include respond-to("laptop") {
        grid-row: 3;

        &_once {
            grid-row: 4;
            padding-top: 24px;
        }

        &_with-teeth {
            grid-row: 5;
            padding-bottom: 65px;
        }

        &_with-old-price {
            padding-bottom: 44px;
        }

        &_once-with-teeth {
            grid-row: 6;
        }
    }
}

.helper {
    margin-left: 10px;

    &__content {
        font-size: 13px;
        line-height: 20px;
    }
}

.legal {
    margin-bottom: 0;
    font-size: 17px;
    line-height: 24px;
    font-weight: 400;
    display: none;

    @include respond-to("mobile") {
        padding: 0 20px;
        text-align: center;
    }

    @include respond-to("laptop") {
        max-width: 272px;
        display: block;
        grid-row: 3;

        &_price {
            grid-row: 4;
        }

        &_with-teeth {
            grid-row: 5;
        }

        &_price-with-teeth {
            grid-row: 6;
        }
    }
}

.progress-title {
    margin-bottom: 16px;
}

.button-container {
    &_hide-mobile {
        display: none;

        @include respond-to("laptop") {
            display: block;
        }
    }
}

.prices-footer {
    justify-items: flex-start;
    border-top: 1px solid rgba(66, 78, 88, 0.1);
    margin-top: 24px;
    font-size: 13px;
    line-height: 20px;

    @include respond-to("laptop") {
        display: block;
        grid-column: 1/-1;
        width: 100%;
        margin-top: 28px;
        font-size: 17px;
        line-height: 24px;
    }

    p {
        margin: 0;
    }
}

.fv-prepayment {
    padding-top: 8px;

    @include respond-to("laptop") {
        padding-top: 16px;
    }
}
