@import "style/mixins.module";

.payment-monthly {
    font-size: 13px;
    line-height: 20px;

    @include respond-to("laptop") {
        margin-bottom: 30px;
        padding-bottom: 16px;
        border-bottom: 1px solid rgba(66, 78, 88, 0.1);
    }

    @include respond-to("laptop-L") {
        padding-bottom: 44px;
        font-size: 17px;
        line-height: 24px;
    }

    p {
        margin: 0;
    }

    &__period {
        font-size: 17px;
        line-height: 24px;

        @include respond-to("laptop") {
            font-size: 22px;
            line-height: 32px;
        }
    }

    &__row {
        display: flex;
        align-items: center;

        @media (max-width: 450px) {
            display: block !important;
        }
    }

    &__amount {
        font-weight: bold;
        font-size: 26px;
        line-height: 30px;

        @include respond-to("tablet") {
            font-size: 36px;
            line-height: 40px;
        }
    }

    &__currency {
        display: flex;
        flex-direction: column;
        margin-left: 8px;

        @media (max-width: 450px) {
          margin-left: 0;
        }

        @include respond-to("laptop") {
            min-width: 77px;
            margin-left: 16px;
        }
    }

    &__month {
        font-size: 17px;
        line-height: 24px;

        @include respond-to("laptop") {
            font-weight: 400;
            font-family: Inter, sans-serif;
            font-size: 22px;
            line-height: 32px;
        }
    }
}
