@import "style/mixins.module";

.button {
    @include normalizedButton;

    display: inline-block;
    vertical-align: middle;
    border-radius: 12px;
    width: 24px;
    padding: 0;
    background: get_color("gray", "0");
    cursor: pointer;

    svg {
        display: block;
    }

    &:focus {
        outline: none;
    }

    &:hover {
        background: get_color("gray", "-5");
    }
}

.theme_blue {
    svg {
        background-color: #5c96cb;

        path {
            fill: get_color("gray", "0");
        }
    }
}
