@import "style/mixins.module";

.price {
    margin-bottom: 0;
    font-size: 26px;
    line-height: 30px;

    white-space: nowrap;

    @include respond-to("tablet") {
        font-size: 36px;
        line-height: 40px;
    }

    @include respond-to("laptop") {
        font-size: 56px;
        line-height: 64px;
    }
}
