@import "style/mixins.module.scss";

.root {
    position: relative;
    max-width: 375px;
    min-height: 50vh;
    margin-left: auto;
    margin-right: auto;

    text-align: left;
}

.form {
    width: 100%;
    min-height: 588px;

    margin: 0 auto;

    &_inter {
        font-family: Inter, sans-serif;
        font-weight: 400;
    }
}

.title {
    margin-bottom: 16px;
    text-align: left;

    &_black {
        @include color("gray", "-5");
    }

    &_ss-pro {
        font-family: "Source Serif Pro", serif;
    }

    &_germany {
        @include color("gray", "-7");
    }
}

.header {
    padding-bottom: 24px;
}
